@media screen and (min-width: 320px) {
  .itemDay {
    border: 2px solid lightgray;
    border-radius: 10px;
    width: 95%;
    margin: auto;
  }

  p {
    font-size: 17px;
    font-weight: bold;
    margin-bottom: 10px;
  }

  .date{
    text-align: center;
    border: 2px solid lightgray;
  }

  .weatherSection{
    padding: 30px 10px 10px;
    border-radius: 10px;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.7);
  }

  .weatherSection2{
    z-index: -1;
    opacity: 0.5;
  }

  .left {
    margin-right: 7px;
  }

  .right {
    text-align: left;
  }

  .nameTime {
    text-align: center;
    margin-bottom: 10px;
  }

  .blockFlex {
    display: flex;
    justify-content: space-around;
  }

  .conditionText {
    font-weight: bold;
    font-size: 30px;
  }

  .temp_c{
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 10px;
  }

  .conditionIcon {
    width: 150px;
    height: 150px;
  }

  .fl {
    margin: auto;
    text-align: center;
    margin-bottom: 40px;
  }

}

@media screen and (min-width: 480px) {
  p {
    font-size: 25px;
  }

  .left {
    margin-right: 30px;
  }


}

@media screen and (min-width: 760px) {
  .itemDay {
    width: 80%;
    color: white;
    font-weight: bold;
  }

  .left {
    color: #300f6f;;
  }


}
