@media screen and (min-width: 320px) {
  .quote {
    width: 90%;
    margin: auto;
  }

  hr {
    margin-bottom: 40px;
  }
  .titleQuote {
    margin-bottom: 20px;
    font-weight: bold;
    color: darkblue;
  }

  .bodyQuote {
    font-weight: bold;
    font-style: italic;
    margin-bottom: 30px;
  }

  .author {
    font-size: 15px;
    text-align: right;
  }
}
