@media screen and (min-width: 320px){
  .flex {
    display: flex;
    justify-content: space-around;
    margin-bottom: 40px;
  }

  .buttons {
    margin: auto;
  }

  .fl {
    text-align: center;
  }

  .nameToday {
    color: white;
    font-weight: bold;
    font-size: 15px;
    border: 2px solid white;
    width: 160px;
    /*width: 50%;*/
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: rgba(236, 240, 241,0.2);
  }
}

@media screen and (min-width: 480px) {
  .nameToday {
    color: white;
    font-weight: bold;
    font-size: 20px;
    border: 2px solid white;
    width: 230px;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: rgba(236, 240, 241,0.2);
  }
}


@media screen and (min-width: 1200px){
  .flex {
    display: flex;
    justify-content: space-around;
    margin-bottom: 40px;
  }

  .buttons {
    /*width: 250px;*/
    max-width: 1300px;
    margin: auto;
  }

  .fl {
    margin: auto;
    text-align: center;
  }

  .nameToday {
    font-weight: bold;
    border: 2px solid gray;
    width: 300px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

}
