*{
  margin: 0;
  box-sizing: border-box;
}

@media screen and (min-width: 320px){
  .inputLocal{
    border: 2px solid #bababa;
    width: 75%;
    font-size: 20px;
    padding: 10px;
    border-radius: 10px;
    background-color: rgba(236, 240, 241,0.2);
    color: white;
    margin-bottom: 8vh;
  }

  .inputLocal::placeholder {
    color: white;
  }

  .locality {
    margin-bottom: 5vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
  }

  .cityCard {
    display: flex;
    flex-direction: column;
    align-items: center;

  }

  .wrap {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    font-weight: bold;
    color: #300f6f;
    width: 90%;
  }

  .wrap p{
    font-size: 40px;
  }

  .imgWrap img{
    margin-right: 20px;
  }

  .localTime {
    font-size: 17px;
  }

}
@media screen and (min-width: 760px){
  .wrap p{
    font-size: 50px;
  }
  .localTime {
    font-size: 20px;
    font-weight: bold;
  }
}
@media screen and (min-width: 1200px) {
  .inputLocal{
    background-color: initial;
    border: 2px solid grey;
    width: 400px;
    font-size: 15px;
    padding: 10px;
    border-radius: 10px;

  }
  .localTime {
    font-size: 25px;
  }

}



