*{
  margin: 0;
 box-sizing: border-box;
}
.itemWrap {
  margin-bottom: 20px;
  width: 350px;
}

.date{
  text-align: center;
  border: 1px solid black;
  width: 100%;
  background-color: deepskyblue;
  border-radius: 10px;
  box-shadow: 0 8px 6px -6px black
}

.weatherSection{
  padding: 10px;
  /*border-bottom: 2px solid black ;*/
  outline: 1px solid black;
}

.left {
  text-align: left;
}
.right {
  text-align: left;
}

.nameTime {
  text-align: center;
  margin-bottom: 10px;
}

.blockFlex {
  display: flex;
  justify-content: space-around;
  /*width: 200px;*/
  width: 100%;
}

.conditionIcon {
  width: 35px;
  height: 35px;
}

.itemDay {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-around;
}