*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.App {
  text-align: center;
}

.mainWrap {
  background-image: url("./images/bg-main.jpg");
  background-size: cover;
  background-repeat: repeat;
  width: 100%;
  padding-top: 10vh;
  height: 100%;
}

.loading {
  margin-bottom: 1vh;
  color: mediumpurple;
}

.wrapLoader{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.loadBackground {
  background-color: black;

}
